<template>
  <Breadcrumb_t msg="Vue Todo List"/>
  <section class="todoapp">
    <header class="header">
      <h1>todos</h1>
      <div class="input-group mb-3">
        <span class="input-group-text" id="inputGroup-sizing-default"
          >Add new task</span
        >
        <input
          class="new-todo form-control"
          autofocus
          placeholder="What needs to be done?"
          @keyup.enter="addTodo"
          type="text"
          aria-label="Sizing example input"
          aria-describedby="inputGroup-sizing-default"
        />
      </div>
    </header>
    <section class="main" v-show="todos.length">
      <div class="form-check">
        <input
          class="form-check-input"
          type="checkbox"
          :checked="remaining === 0"
          @change="toggleAll"
          id="toggle-all"
        />
        <label class="form-check-label" for="toggle-all">
          Mark all as complete
        </label>
      </div>
      <TransitionGroup
        tag="ul"
        name="fade"
        class="container list-group todo-list"
      >
        <li
          v-for="todo in filteredTodos"
          class="list-group-item todo"
          :key="todo.id"
          :class="{ completed: todo.completed, editing: todo === editedTodo }"
        >
          <div class="view">
            <div class="form-check">
              <input
                class="toggle form-check-input"
                type="checkbox"
                v-model="todo.completed"
              />
              <label
                >{{ todo.title }}
                </label
              >
              <a class="destroy float-end ps-2" @click="removeTodo(todo)"
                ><i class="bi bi-x" style="color:red;"></i
              ></a>
              <a class="ps-2 float-end" @click="editTodo(todo)">Edit</a>
              
            </div>
          </div>
          <input
            v-if="todo === editedTodo"
            class="edit form-control my-2"
            type="text"
            v-model="todo.title"
            @vnode-mounted="({ el }) => el.focus()"
            @blur="doneEdit(todo)"
            @keyup.enter="doneEdit(todo)"
            @keyup.escape="cancelEdit(todo)"
          />
        </li>
      </TransitionGroup>
    </section>
    <footer class="footer" v-show="todos.length">
      <span class="todo-count">
        <strong>{{ remaining }}</strong>
        <span>{{ remaining === 1 ? " item" : " items" }} left</span>
      </span>
      <div class="filters">
        <a href="javscript:void(0)" class="me-2" @click="fTodo('all')"
          ><span class="badge rounded-pill bg-primary">All</span></a
        >
        <a href="javscript:void(0)" class="me-2" @click="fTodo('active')"
          ><span class="badge rounded-pill bg-secondary">Active</span></a
        >
        <a href="javscript:void(0)" class="me-2" @click="fTodo('completed')"
          ><span class="badge rounded-pill bg-success">Completed</span></a
        >
      </div>
      <div>
      <button
        class="mt-4 clear-completed btn btn-danger"
        @click="removeCompleted"
        v-show="todos.length > remaining"
      >
        Clear completed
      </button>
      </div>
    </footer>
  </section>
</template>

<script>
/* eslint-disable */
import Breadcrumb_t from "@/components/Breadcrumb_t.vue"

const STORAGE_KEY = "vue-lists"

const filters = {
  all: (todos) => todos,
  active: (todos) => todos.filter((todo) => !todo.completed),
  completed: (todos) => todos.filter((todo) => todo.completed),
};

export default {
  name: "HelloWorld",
  props: {
    msg: String,
  },
  components: {
    Breadcrumb_t,
  },
  data: () => ({
    todos: JSON.parse(localStorage.getItem(STORAGE_KEY) || "[]"),
    editedTodo: null,
    visibility: "all",
  }),
  watch: {
    todos: {
      handler(todos) {
        localStorage.setItem(STORAGE_KEY, JSON.stringify(todos));
      },
      deep: true,
    },
  },
  mounted() {
    window.addEventListener("hashchange", this.onHashChange);
    this.onHashChange();
  },
  computed: {
    filteredTodos() {
      return filters[this.visibility](this.todos);
    },
    remaining() {
      return filters.active(this.todos).length;
    },
  },
  methods: {
    fTodo(f){
      this.visibility = f;
      return filters[this.visibility](this.todos);
    },
    toggleAll(e) {
      this.todos.forEach((todo) => (todo.completed = e.target.checked));
    },
    addTodo(e) {
      const value = e.target.value.trim();
      if (!value) {
        return;
      }
      this.todos.push({
        id: Date.now(),
        title: value,
        completed: false,
      });
      e.target.value = "";
    },
    removeTodo(todo) {
      this.todos.splice(this.todos.indexOf(todo), 1);
    },
    editTodo(todo) {
      this.beforeEditCache = todo.title;
      this.editedTodo = todo;
    },
    doneEdit(todo) {
      if (!this.editedTodo) {
        return;
      }
      this.editedTodo = null;
      todo.title = todo.title.trim();
      if (!todo.title) {
        this.removeTodo(todo);
      }
    },
    cancelEdit(todo) {
      this.editedTodo = null;
      todo.title = this.beforeEditCache;
    },

    removeCompleted() {
      this.todos = filters.active(this.todos);
    },
    onHashChange() {
      var visibility = window.location.hash.replace(/#\/?/, "");
      if (filters[visibility]) {
        this.visibility = visibility;
      } else {
        window.location.hash = "#/about";
        this.visibility = "all";
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
/* 1. declare transition */
.fade-move,
.fade-enter-active,
.fade-leave-active {
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
}

/* 2. declare enter from and leave to state */
.fade-enter-from,
.fade-leave-to {
  opacity: 0;
  transform: scaleY(0.01) translate(30px, 0);
}

/* 3. ensure leaving items are taken out of layout flow so that moving
      animations can be calculated correctly. */
.fade-leave-active {
  position: absolute;
}
</style>
